'use client';

import { useEffect, useState } from 'react';

export default function ReviewSignupsPage() {
  const [signups, setSignups] = useState([]);
  const [loading, setLoading] = useState(true);

  const fetchSignups = async () => {
    const res = await fetch('/api/signups-overview');
    const data = await res.json();
    setSignups(data);
    setLoading(false);
  };

  const handleAdd = async (vol) => {
    const res = await fetch('/api/add-to-directory', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ name: vol.name, phone: vol.phone }),
    });
    const result = await res.json();
    alert(result.message);
  };

  const handleConfirm = async (vol) => {
    const res = await fetch('/api/confirm-to-event', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ name: vol.name, event: vol.event }),
    });
    const result = await res.json();
    alert(result.message);
    fetchSignups(); // refresh spotsLeft
  };

  useEffect(() => {
    fetchSignups();
  }, []);

  if (loading) return <div className="p-4 text-gray-500">Loading signups‚Ä¶</div>;

  return (
    <div className="p-6 max-w-7xl mx-auto">
      <h1 className="text-2xl font-bold mb-6">üßë‚Äçüç≥ Review Volunteer Signups</h1>
      <table className="w-full border text-sm">
        <thead className="bg-gray-100">
          <tr>
            <th className="p-2 text-left">Name</th>
            <th className="p-2">Phone</th>
            <th className="p-2">Email</th>
            <th className="p-2">Event</th>
            <th className="p-2">Signed Up</th>
            <th className="p-2">Rating</th>
            <th className="p-2">Last Volunteered</th>
            <th className="p-2">Spots Left</th>
            <th className="p-2">Actions</th>
          </tr>
        </thead>
        <tbody>
          {signups.map((vol, i) => (
            <tr key={i} className="border-t">
              <td className="p-2">{vol.name}</td>
              <td className="p-2 text-center">{vol.phone}</td>
              <td className="p-2 text-center">{vol.email}</td>
              <td className="p-2 text-center">{vol.event}</td>
              <td className="p-2 text-center">{vol.date}</td>
              <td className="p-2 text-center">{vol.rating || '‚Äî'}</td>
              <td className="p-2 text-center">
                {vol.lastEvent ? `${vol.lastEvent} (${vol.lastDate})` : '‚Äî'}
              </td>
              <td className="p-2 text-center">{vol.spotsLeft}</td>
              <td className="p-2 text-center">
                <button
                  className="bg-green-500 hover:bg-green-600 text-white px-2 py-1 rounded mr-2"
                  onClick={() => handleAdd(vol)}
                >
                  Add to Directory
                </button>
                <button
                  className={`${
                    vol.spotsLeft > 0 ? 'bg-blue-500 hover:bg-blue-600' : 'bg-gray-300 cursor-not-allowed'
                  } text-white px-2 py-1 rounded`}
                  onClick={() => vol.spotsLeft > 0 && handleConfirm(vol)}
                  disabled={vol.spotsLeft === 0}
                >
                  Confirm to Event
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}